
.blob-size-switcher {
  margin-left: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.blob-size-switcher span.information {
    font-size: 0.9rem;
    margin-right: 20px;
}
.blob-size-switcher .blob-size-box {
    padding: 20px 30px;
    background: #f4f4f4;
    border-radius: 13px;
}
.blob-size-switcher .blob-size-box svg.icon {
      margin-right: 10px;
}
.blob-size-switcher .blob-size-box svg.icon:last-of-type {
        margin-right: 0;
}
svg.action {
  cursor: pointer;
}
svg.icon {
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  color: #22242a;
}
svg.icon:hover {
    color: #9eeec7;
}
svg.icon.small {
    width: 10px;
    height: 10px;
}
svg.icon.medium {
    width: 15px;
    height: 15px;
}
svg.icon.large {
    width: 24px;
    height: 24px;
}
svg.icon.active {
    color: #73e6ae;
    fill: currentColor;
}
body.theme-light svg.icon {
  color: #f4f4f4;
}
body.theme-light svg.icon:hover {
    color: #9eeec7;
}
body.theme-light svg.icon.active {
    color: #73e6ae;
    fill: currentColor;
}
body.theme-light .blob-size-switcher .blob-size-box {
  background: #22242a;
}



