
li.chat-message {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0;
  overflow: hidden;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
li.chat-message:first-of-type {
    margin-top: 60px;
}
li.chat-message .blob-chat {
    width: 200px;
    text-align: center;
}
li.chat-message.bot-message {
    margin: 20px;
    padding: 20px;
    background: rgba(25, 27, 34, 0.3);
    border-radius: 13px;
}
li.chat-message,
  li.chat-message span.author {
    font-size: 1.2rem;
}
li.chat-message span.timestamp {
    font-size: 0.9rem;
    margin-left: 5px;
}
li.chat-message p.body {
    margin: 0;
    max-width: 1000px;
    word-wrap: break-word;
}
li.chat-message.small {
    max-height: 80px;
}
li.chat-message.small p.body {
      font-size: 1rem;
      line-height: 2rem;
}
li.chat-message.small .chat-message-content {
      margin-left: -30px;
}
li.chat-message.small .blob-chat {
      margin-left: -37px;
}
li.chat-message.medium {
    max-height: 180px;
}
li.chat-message.medium p.body {
      font-size: 2rem;
      font-weight: 500;
      line-height: 80px;
}
li.chat-message.medium .chat-message-content {
      margin-left: 10px;
}
li.chat-message.medium .blob-chat {
      margin-left: -30px;
}
li.chat-message.large {
    max-height: 280px;
}
li.chat-message.large p.body {
      font-size: 5rem;
      font-weight: 700;
      line-height: 186px;
}
li.chat-message.large .blob-chat {
      margin-left: -35px;
}
li.chat-message.large .chat-message-content {
      margin-left: 100px;
}



