.users-list {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding: 20px 60px 20px 40px;
  margin: 50px;
  border-radius: 13px;
  background: #22242a;
}
.users-list ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
.users-list ul li {
      margin: 10px;
}
.users-list ul li span.circle {
        position: relative;
}
.users-list ul li span.circle:before {
          content: "";
          position: absolute;
          top: 2px;
          left: -25px;
          width: 15px;
          height: 15px;
          border-radius: 100%;
          background: #f9f9f9;
}
body.theme-light .users-list {
  background: #f9f9f9;
}



