
.blob-banner {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 10%;
  -webkit-animation: blobAnimation 100s infinite;
          animation: blobAnimation 100s infinite;
}
@-webkit-keyframes blobAnimation {
0% {
    top: 0;
}
50% {
    top: -100vh;
}
100% {
    top: 0;
}
}
@keyframes blobAnimation {
0% {
    top: 0;
}
50% {
    top: -100vh;
}
100% {
    top: 0;
}
}



