
.blur-enter-active, .blur-leave-active {
  -webkit-transition: opacity .5s, -webkit-filter 0.3s, -webkit-transform 0.5s;
  transition: opacity .5s, -webkit-filter 0.3s, -webkit-transform 0.5s;
  transition: opacity .5s, filter 0.3s, transform 0.5s;
  transition: opacity .5s, filter 0.3s, transform 0.5s, -webkit-filter 0.3s, -webkit-transform 0.5s;
}
.blur-enter, .blur-leave-to {
  -webkit-transform: scale3d(0.8, 0.8, 0.8);
          transform: scale3d(0.8, 0.8, 0.8);
  -webkit-transform-origin: left center;
          transform-origin: left center;
  opacity: 0;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  position: relative;
}
.chat-room {
  max-height: calc(100vh - 280px);
  overflow: auto;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 0;
  margin: 0;
}
body.theme-light span.is-writing {
  color: #22242a;
}



